<!-- 递归二级列表 -->
<template>
	<div>
		<!-- 循环一级列表 -->
	  <div
	    class="item"
	    v-for="(item, index) of list"
	    :key="index"
	  >
	    <div class="item-title border-bottom">
	      <span class="item-title-icon"></span>
		  <!-- 一级列表-->
	      {{item.title}}
	    </div>
		<!-- 如果item里还有一个childern，那么循环二级列表 -->
	    <div v-if="item.children" class="item-chilren">
	      <detail-list :list="item.children"></detail-list><!-- 递归组件？ -->
	    </div>
	  </div>
	</div>
</template>

<script>
	export default{
		name:'DetailList',
		props: {
			list: Array
		},
	}
</script>

<style lang="stylus" scoped>
  .item-title-icon
    position: relative
    left: .06rem
    top: .06rem
    display: inline-block
    width: .36rem
    height: .36rem
    background: url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
    margin-right: .1rem
    background-size: .4rem 3rem
  .item-title
    line-height: .8rem
    font-size: .32rem
    padding: 0 .2rem
  .item-chilren
    padding: 0 .2rem
</style>



